import ReactMarkdown from 'react-markdown';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { tomorrow as tomorrowStyle } from 'react-syntax-highlighter/dist/cjs/styles/prism';
import type { ComponentPropsWithoutRef } from 'react';
import remarkGfm from 'remark-gfm';

interface CodeProps extends ComponentPropsWithoutRef<'code'> {
  inline?: boolean;
}

interface ChatMessagesProps {
  messages: Array<{
    role: 'user' | 'assistant';
    content: string;
  }>;
}

export default function ChatMessages({ messages }: ChatMessagesProps) {
  return (
    <div className="h-full px-4">
      <div className="max-w-3xl mx-auto w-full py-4 space-y-4">
        {messages.map((message, index) => (
          <div
            key={index}
            className={`flex ${message.role === 'user' ? 'justify-end' : 'justify-start'}`}
          >
            <div
              className={`max-w-[80%] p-4 rounded-lg prose prose-sm ${
                message.role === 'user'
                  ? 'bg-blue-500 prose-headings:text-white prose-p:text-white prose-strong:text-white prose-em:text-white prose-code:text-white prose-a:text-white prose-li:text-white prose-blockquote:text-white prose-pre:bg-blue-600'
                  : 'bg-gray-100 dark:bg-gray-800 dark:prose-invert'
              }`}
            >
              <ReactMarkdown
                remarkPlugins={[remarkGfm]}
                components={{
                  code: ({ inline, className, children, ...props }: CodeProps) => {
                    const match = /language-(\w+)/.exec(className || '');
                    return !inline && match ? (
                      <SyntaxHighlighter
                        {...props}
                        style={tomorrowStyle}
                        language={match[1]}
                        PreTag="div"
                      >
                        {String(children).replace(/\n$/, '')}
                      </SyntaxHighlighter>
                    ) : (
                      <code 
                        className={`${className} ${
                          message.role === 'user' 
                            ? 'bg-blue-400' 
                            : 'bg-gray-200 dark:bg-gray-700'
                        } rounded px-1`} 
                        {...props}
                      >
                        {children}
                      </code>
                    );
                  }
                }}
              >
                {message.content}
              </ReactMarkdown>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
} 